@import url('https://fonts.googleapis.com/css2?family=Fraunces:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

h1{
    font-family: 'Fraunces', serif;
    font-weight: 700;
}
p{
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 14px;
}

body{

    background-color:  hsl(30, 38%, 92%);
}
#Tudo{
    display: flex;
    width: 600px;
    height: 450px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 175px;
    background-color: white;
    border-radius: 10px;
    & #imge{
        height: auto;
        width: 300px;
        margin: 0px;
        padding: 0px;
            & img{
                height: 450px;
                width: 300px;
                margin: 0px;
                padding: 0px;
                border-radius: 10px 0px 0px 10px;
            }
    } 
}
#conteudo{
    padding: 0px;
    margin: 25px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

#preco{
    align-items: center;
    display: flex;
    justify-content: space-between;
    border: 0;
    width: 250px;
    height: 25px;
    & #promo{
        font-size: 25px;
        color: #327c5e;
        margin-top: 7.5px;
        font-family: 'Fraunces', serif;
        font-weight: 700;
    }
    & #precoR{
        margin-top: 13px;
        font-size: 15px;
        text-decoration: line-through;
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        color: gray;
    }
}

#desc{
    border: 0;   
    display: flex; 
    flex-wrap: wrap;
    & h1{
        font-size: 30px;
        line-height: 30px;
        margin: 5px;
    }
    & p{
        color:gray;
        font-size: 14px;
        line-height: 25px;
        margin: 10px;
    }
}
#compra{
    display: flex;
    justify-content: flex-start;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    background-color: #327c5e;
    color: white;
    width: 250px;
    height: 45px;
    border-radius: 10px;
    margin-bottom: 9px;
    & *{
        margin-left: 35px;
        margin-top: 10px;
    }
    & img{
        width: 20px;
        height: 20px;
        margin-right: 10px;
    }
    & p{
        margin-top: 12px;
    }
}

